import React from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useEffect } from "react";

const Moi = () => {
  gsap.registerPlugin(ScrollTrigger);
  useEffect(() => {
    const anim = () => {
      const tl = gsap.timeline();
      tl.fromTo(
        ".cont",
        {
          opacity: 0,
        },
        {
          opacity: 1,
          duration: 1,
          ease: "power2.inOut",
          stagger: 0.25,
        }
      );
    };
    anim();
  }, []);

  return (
    <div className="box3">
      <div className="cont">
        <img className="stagger3" src="./1.jpg" alt="" />
        <p>Aventurier</p>
      </div>
      <div className="cont">
        <img src="./2.jpg" alt="" className="stagger3" />
        <p>Papa poule</p>
      </div>
      <div className="cont">
        <video
          src="./video.mp4"
          className="stagger3"
          autoPlay
          loop
          muted
        ></video>
       <span>Maraicher du dimanche</span>
      </div>
    </div>
  );
};

export default Moi;
